body {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: "Inter",serif;
    background-color: red;
}

.Section-title {
    background: url("/img/arkaplan.png") no-repeat center center / cover;
    height: 100vh;
    width: 100%;

    &-element {

        &-logo {
            height: 50px;
            position: relative;
            top: 20px;
            left: 10px;

            @include mq() {
                height: 75px;
                top: 30px;
            }
        }

        &-title {
            height: 78vh;
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;

            &-svg {
                height: 100%;
                width: 100%;

                @include mq() {
                    height: 400px;
                    top: 30px;
                }
            }
        }

        &-text {}
    }
}

.Section-text {
    margin-top: 50px;
    max-width: 770px;
    margin-left: auto;
    margin-right: auto;

    &-p {
        color: rgb(115 121 132);
        font-weight: 400;
        text-align: center;
        display: flex;
        flex-direction: column;
        gap: 10px;
    }

    &-img {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: 50px;
        position: relative;

        &-icon {
            background-color: rgba(white, .3);
            width: 50px;
            height: 50px;
            position: absolute;
            border-radius: 100px;
            display: flex;
            align-items: center;
            justify-content: center;

            @include mq() {
                height: 75px;
                width: 75px;
            }

            &:hover {
                background-color: rgba(black, 0.3);
                border: 2px solid #80BC00;
            }
        }

        &-photo {
            width: 100%;
            border-radius: 10px;

            @include mq() {
                width: 70%;
                display: flex;
                margin-left: auto;
                margin-right: auto;
            }
        }

        &-backicon {
            position: absolute;
            z-index: -1;
        }

        .lightning {
            height: 150px;
            width: 150px;
            position: absolute;
            left: -30px;
            bottom: 90px;
            opacity: 0.5;

            @include mq() {
                height: 200px;
                width: 200px;
                bottom: 180px;
            }
        }

        .leaf {
            height: 150px;
            width: 150px;
            position: absolute;
            left: 150px;
            top: 100px;
            opacity: 0.5;

            @include mq() {
                height: 200px;
                width: 200px;
                left: 550px;
                top: 150px;
            }
        }
    }
}

.section-panel {


    &-surec {
        position: relative;

        .div {
            position: relative;
            height: 150px;
            display: flex;
            margin-top: 350px;
            margin-bottom: 50px;

            @include mq() {
                margin-top: 50px;
                margin-bottom: 100px;
            }

            .surec-panel {
                height: 60px;
                position: absolute;
                bottom: 320px;
                display: flex;
                justify-content: center;
                order: 1;
                width: 100%;

                @include mq() {
                    width: 70%;
                    height: 100px;
                    display: flex;
                    justify-content: flex-start;
                    position: absolute;
                    top: 50px;
                    left: 100px;
                }
            }

            .surec-panel1 {

                @include mq () {
                    display: flex;
                    justify-content: flex-end;
                }
            }


        }

        &-div1 {

            .card {
                width: 90%;
                height: 150px;
                background-color: rgba(#80BC00, .2);
                display: flex;
                margin-left: auto;
                margin-right: auto;
                order: 2;
                border: 0;
                color: #80BC00;
                align-items: center;
                justify-content: center;
                text-align: center;
                padding: 10px;
                font-size: 0.7em;
                font-family: "Inter",serif;
                border-radius: 15px;
                position: relative;

                @include mq() {
                    width: 50%;
                    height: 200px;
                    margin-left: 0;
                    margin-right: auto;

                }
            }

            .surec-panel::after {
                content: " ";
                display: block;
                height: 200px;
                width: 10px;
                background-color: rgba(#7B98CE, .3);
                position: relative;
                right: 57px;
                z-index: -1;
                top: 30px;

                @include mq() {
                    width: 10px;
                    height: 120px;
                    position: relative;
                    top: -100px;
                    right: 70px;
                }
            }

            .surec-panel::before {
                content: " ";
                display: block;
                height: 150px;
                width: 10px;
                background-color: rgba(#7B98CE, .3);
                position: relative;
                left: 62px;
                z-index: -1;
                bottom: 120px;

                @include mq() {
                    width: 33.4%;
                    height: 10px;
                    position: relative;
                    top: 50%;
                }

                @include mq("özel") {
                    width: 34%;
                }

                @include mq("özel2") {
                    width: 35%;
                }

                @include mq("özel3") {
                    width: 38%;
                }
            }
        }

        &-div2 {

            .card {
                width: 90%;
                height: 150px;
                background-color: rgba(#80BC00, .2);
                display: flex;
                margin-left: auto;
                margin-right: auto;
                border: 0;
                color: #80BC00;
                align-items: center;
                justify-content: center;
                text-align: center;
                padding: 10px;
                font-size: 0.7em;
                font-family: "Inter",serif;
                border-radius: 15px;

                @include mq() {
                    width: 50%;
                    height: 200px;
                    margin-left: auto;
                    margin-right: 0;
                }

                &-text {
                    font-family: "Inter",serif;
                }
            }

            .surec-panel::after {
                content: " ";
                display: block;
                height: 200px;
                width: 10px;
                background-color: rgba(#7B98CE, .3);
                position: relative;
                right: 57px;
                z-index: -1;
                top: 30px;

                @include mq() {
                    width: 53.5%;
                    height: 10px;
                    position: relative;
                    top: 50%;
                }

                @include mq("özel") {
                    width: 51%;
                }

                @include mq("özel2") {
                    width: 47%;
                }

                @include mq("özel3") {
                    width: 38%;
                }
            }

            .surec-panel::before {
                content: " ";
                display: block;
                height: 150px;
                width: 10px;
                background-color: rgba(#7B98CE, .3);
                position: relative;
                left: 62px;
                z-index: -1;
                bottom: 120px;

                @include mq() {
                    height: 120px;
                    width: 10px;
                    position: relative;
                    bottom: 100px;
                    left: 70px;
                }
            }
        }
    }

    .pano {
        display: flex;
        justify-content: center;
        position: relative;

        @include mq() {
            padding: 90px;
            background-image: url("/img/yıldırım.svg"), url("/img/koyuyaprak.svg");
            background-repeat: no-repeat;
            background-position-x: 350px, 800px;
            background-position-y: 20px, 120px;
        }

        &-card {
            width: 90%;
            height: 10em;
            border-radius: 20px;
            background-color: #7B98CE;
            color: #fff;
            font-family: "Inter",serif;
            display: flex;
            align-items: center;

            @include mq() {
                width: 500px;
                height: 10em;
            }

            &-text {
                text-align: center;
                font-size: 1.1em;
                margin: 0;
                padding: 5px;
                margin-left: auto;
                margin-right: auto;

                @include mq() {
                    padding: 20px;
                    font-size: 1.4em;
                }
            }
        }
    }
}

.h-title {
    font-family: 'Quicksand',sans-serif;
    color: #80BC00;
    font-size: 2.7em;
    display: flex;
    justify-content: center;
    margin-bottom: 1rem;
    margin-top: 50px;
    text-align: center;
    font-weight: 600;

    @include mq() {
        font-size: 80px;
    }
}

.panel {
    height: 85px;
    box-sizing: content-box;
}

.paragraph {
    color: rgba(10, 38, 72);
    opacity: 0.5;
    width: 90%;
    display: flex;
    margin-left: auto;
    margin-right: auto;

    @include mq() {
        width: 60%;
    }
}

.paragraph-text {
    margin-bottom: 25px;
    margin-top: 25px;
    font-family: "Inter", serif;
}

