.footer {
    width: 100%;
    height: 550px;
    background-color: #2D2D2D;
    margin-top: 100px;

    @include mq() {
        height: 230px;
        display: flex;
        align-items: center;
    }

    &-element {
        display: flex;
        flex-direction: column;
        align-items: center;

        @include mq() {
            flex-direction: row;
            width: 90%;
            margin-left: auto;
            margin-right: auto;
        }

        .ftr-yardım {
            height: 150px;
            width: 150px;
            margin-top: 50px;
            border-bottom: 2px solid #fff;
            padding-bottom: 20px;

            @include mq() {
                border: 0;
                border-right: 2px solid #fff;
                margin: 0;
                padding-right: 30px;
            }
        }

        .ftr-iilk {
            height: 150px;
            width: 150px;
            border-bottom: 2px solid #fff;

            @include mq() {
                width: 300px;
                border: 0;
                margin: 0;
                padding-left: 30px;
                padding-right: 20px;
            }
        }

        .ftr-link {
            display: flex;
            flex-direction: column;
            text-align: center;
            gap: 10px;
            margin-top: 10px;

            @include mq() {
                margin-left: auto;
                margin-right: 0;
                text-align: start;
            }

            .link-item {
                text-decoration: none;
                color: #fff;
            }
        }
    }
}